
"use client";

import SectionTitle from '@/components/shared/SectionTitle';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Skeleton } from '@/components/ui/skeleton';
import { Button } from '@/components/ui/button';
import { Utensils, ListOrdered, TicketPercent } from 'lucide-react';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';

export default function DashboardPage() {
  const [userName, setUserName] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const router = useRouter();

  useEffect(() => {
    const name = localStorage.getItem("userName");
    setUserName(name);
    setIsLoading(false);
  }, []);

  if (isLoading) {
    return (
       <div className="flex-1 space-y-8 p-4 md:p-8 pt-6">
        <div className="flex items-center justify-between space-y-2">
          <Skeleton className="h-10 w-1/3" />
        </div>
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[...Array(3)].map((_, i) => (
             <Card key={i}>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                    <Skeleton className="h-6 w-2/3" />
                    <Skeleton className="h-6 w-6 rounded-full" />
                </CardHeader>
                <CardContent>
                    <Skeleton className="h-8 w-1/2" />
                    <Skeleton className="h-4 w-full mt-2" />
                </CardContent>
            </Card>
          ))}
        </div>
      </div>
    )
  }

  return (
    <div className="flex-1 space-y-8 p-4 md:p-8 pt-6">
      <div className="flex items-center justify-between space-y-2">
        <SectionTitle 
            title={`Welcome back, ${userName || 'User'}!`}
            subtitle="Here's a quick overview of your account."
        />
      </div>
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              Quick Actions
            </CardTitle>
            <Utensils className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">Find Food</div>
            <p className="text-xs text-muted-foreground">
              Browse vendors and find your next meal
            </p>
             <Button size="sm" className="mt-4" onClick={() => router.push('/vendors')}>
              Browse Vendors
            </Button>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              My Orders
            </CardTitle>
            <ListOrdered className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">No Active Orders</div>
            <p className="text-xs text-muted-foreground">
              View your past and current orders
            </p>
             <Button size="sm" variant="outline" className="mt-4" onClick={() => router.push('/orders')}>
              View Orders
            </Button>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              My Promotions
            </CardTitle>
            <TicketPercent className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">0 Vouchers</div>
            <p className="text-xs text-muted-foreground">
              See your available discounts
            </p>
             <Button size="sm" variant="outline" className="mt-4" onClick={() => router.push('/promotions')}>
              View Promotions
            </Button>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
